body, input {
  font-family: 'Pangolin', sans-serif;
  background-color:#3a555d;
  font-size: 30px;
}

button {
  border-width: 0;
  border-radius: 50%;
  background-color: #96abb1;
  color: #3a555d;
  cursor: pointer;
  transition: .2s all ease;

  &:hover {
    color: lighten($color: #3a555d, $amount: 20);
    background-color: lighten($color: #96abb1, $amount: 20);
  }

}

fieldset,input,form {
  border-collapse: collapse;
  border-color: #3a555d;
  border-style: none;
  background-color: transparent;
}

input {
  padding: 1rem;
  border-radius: 1rem;
  background-color: rgba(0, 0, 0, 0.1);
  
  &:focus {
    outline: none;
    background-color: rgba(0, 0, 0, 0.05);
  }
}
