.root {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  
  &__container {
    min-width: 50rem;
    background-color: white;
    padding: 2rem;
    border-radius: 1rem;

    h2 {
      text-align: center;
      margin: 0;
    }
  }

  &__add-button {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 2rem;
  }
}